import axios from '@/utils/uniAxios.js'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import {uniSearchBar} from "@/components/uni-search-bar/uni-search-bar.vue";
export default {
	components: {
		uniLoadMore, uniSearchBar
	},
	data() {
		return {
			historyGoods: ['窝窝头', '大闸蟹', '红烧肉', '西班牙大龙虾', '热干面', '牛肉面', '炸酱面'],
			cateMaskState: 0, //分类面板展开状态
			loadingType: 'nomore', //加载更多状态
			filterIndex: 0, 
			productRequest: {
				productCategoryId: '',
				pageSize: 10,
				pageNum: 1,
			}, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: []
		};
	},
	
	onLoad(options){



		this.productRequest.productCategoryId = options.tid;
		// if (options.tid)
		this.loadData('refresh');
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods: {
		//加载分类
		async search() {
			return await axios.post('/product/search', this.productRequest);
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if (type === 'add') {
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			} else {
				this.loadingType = 'more'
			}
			const {data} = await this.search();
			let goodsList = data.code === 200 ? data.data.list : [];
			if (type === 'refresh') {
				this.goodsList = [];
			}
			this.goodsList = this.goodsList.concat(goodsList);
			//判断是否还有下一页，有是more  没有是nomore
			this.loadingType  = this.goodsList.length >= data.data.total - data.data.pageNum ? 'nomore' : 'more';
			if(type === 'refresh'){
				if (loading == 1) {
					uni.hideLoading()
				} else {
					uni.stopPullDownRefresh();
				}
			}
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if (index === 2) {
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			} else {
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){}
	},
}
